.landingPage {
  background-color: var(--background-color);
  position: absolute;
  top: 0;
  left: 200px;
  right: 0;
  height: 100vh;
  overflow: hidden;

  .marginSection {
    margin-left: 4%;
    margin-top: 1%;
    height: fit-content;

    h1 {
      line-height: 60px;
      font-size: 6rem;
      color: var(--text-color);
      width: fit-content;
      margin: 1rem 0 1rem 5%;
      font-family: monospace;

      .colorfulLetter {
        line-height: 8rem;
        padding-top: 3rem;
        font-size: 10rem;
        font-family: monospace;
        color: var(--special);
        text-shadow: -0.8rem -0.3rem 0px var(--accent);
      }
    }

    .backgroundHtmlElement {
      color: var(--background-text);
      font-family: "Freehand", cursive;
      font-size: 2rem;
      line-height: 130%;
    }
    .backgroundHtmlElement:nth-of-type(1) {
      margin-bottom: 2rem;
    }
    .backgroundHtmlElement:nth-of-type(2) {
      margin-left: 2%;
    }
    .backgroundHtmlElement:nth-of-type(3),
    .backgroundHtmlElement:nth-of-type(4),
    .backgroundHtmlElement:nth-of-type(5),
    .backgroundHtmlElement:nth-of-type(6),
    .backgroundHtmlElement:nth-of-type(7) {
      margin-left: 4%;
    }

    .backgroundHtmlElement:nth-of-type(8),
    .backgroundHtmlElement:nth-of-type(9) {
      margin-top: 1rem;
      margin-left: 2%;
    }

    .backgroundHtmlElement:last-of-type {
      margin-top: 2rem;
    }

    .headingAddition {
      margin: 1rem 0 1rem 5%;
      margin-left: 5%;
      font-size: 2rem;
      font-family: monospace;
      color: var(--special-text);
    }

    .contactButton {
      font-family: monospace;
      font-size: 2rem;
      margin: 1rem 0 0 3%;
      width: 200px;
      height: 60px;
      border: 1px solid var(--accent);
      cursor: pointer;
      overflow: hidden;
      transition: 0.5s;
      background: var(--background-color);
      transform: translateZ(0);
      color: var(--accent);

      &::before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;
        background: var(--accent);
        clip-path: polygon(0 0, 0% 0%, -20% 100%, 0% 100%);
        transition: 0.5s;
      }

      &:hover::before {
        clip-path: polygon(0 0, 120% 0%, 100% 100%, 0% 100%);
      }

      &:hover {
        color: var(--background-color);
      }
    }
  }
}
